html {
  min-height: 100%;
}

body {
  background-color: #333;
  background-image: linear-gradient(#033536, #010c12);
  min-height: 100%;
  font-family: arial, sans-serif;
}

#debugApp h2 {
  color: #fff;
}

#debugApp .form-label {
  color: #ccc;
}

#debugApp .tick {
  opacity: .1;
}

#debugApp .tick.tickDown {
  color: red;
}

#debugApp .tick.tickUp {
  color: #0f0;
}

#debugApp .tick.tickAnimate {
  animation: .3s forwards blink;
}

@keyframes blink {
  0% {
    opacity: 1;
  }

  50% {
    opacity: .3;
  }

  100% {
    opacity: .2;
  }
}

#debugApp .sourceIdents img {
  filter: invert();
  max-height: 60px;
  margin-bottom: 15px;
  display: block;
}

#debugApp #sAlbumArtUri .bi.bi-exclamation-circle-fill {
  color: #ff0;
}

#debugApp #sAlbumArtUri .bi.bi-check-circle-fill {
  color: green;
}

#debugApp #sAlbumArtUri .bi.bi-x-circle-fill {
  color: red;
}
